@import '../../../../styles/variables';

.friends-bar__link {
  box-sizing: border-box;
  display: inline-block;
  padding: 17px 10px;
  width: 100%;

  color: inherit;
  text-decoration: none;
  line-height: 26px; }

.friends-bar__icon {
  width: 23px;
  padding-right: 10px;
  float: left;
  fill: $light-gray; }

.friends-bar__link:hover {
  color: $hoverFontColor;
  text-decoration: underline; }

.friends-bar__link:hover .friends-bar__icon {
  fill: $hoverFontColor; }
