@import '../../../../styles/variables';

.owerview {
  padding: 15px;
  background-color: $dark-gray;

  &__header {
    display: flex; }

  &__artist {
    box-sizing: border-box;
    width: 70%;
    padding: 0 15px 30px 0;
    display: inline-block; }

  &__block {
    width: 30%;
    display: inline-block; }

  &__albums {
    display: flex;
    justify-content: space-between;

    border-bottom: 1px solid $mainTheme; }

  &__view {
    width: 30px;
    height: 30px;

    fill: $white;
    border-radius: 50%;

    cursor: pointer; }

  &__view:hover {
    background-color: $label; }

  &__view_active {
    background-color: $mainTheme; }

  &__view_small {
    width: 20px;
    padding: 0 5px; }

  &__track {
    display: flex;
    height: 42px;
    align-items: center;
    justify-content: space-between;

    .owerview__title {
      margin: 0 auto 0 70px; } }

  &__track-number {
    margin-left: 10px; }

  &__track-time {
    fill: $light-gray; }

  &__track-popularity {
    fill: $light-gray;
    margin-left: 46px; } }

@media screen and ( max-width: 1200px ) {
  .owerview {

    &__track-popularity {
      display: none; } } }


@media screen and ( max-width: 1025px ) {
  .owerview {

    &__header {
      display: block;
      margin-bottom: 20px; }

    &__artist {
      width: 100%; }

    &__block {
      width: 100%; } } }


