@import '../../../../styles/variables';

.user {
  width: 300px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__button {
    height: 18px;
    cursor: pointer;
    fill: $light-gray; }

  &__button:hover {
    fill: $white; }

  &__info {
    width: 140px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-image {
      width: 30px;
      border-radius: 50%; } }

  &__menu-button {
    width: 30px;
    height: 30px;
    fill: $light-gray;
    cursor: pointer;

    &:hover {
      fill: $white; } }

  &__menu {
    top: 60px;
    right: 10px;
    position: absolute;
    padding: 0;
    list-style-type: none;
    display: none;

    &-item {
      box-sizing: border-box;
      background-color: $mainTheme;
      padding: 10px;
      width: 160px;
      transition: background-color 0.2s; }

    &-item:hover {
      background-color: $light-gray;
      color: $hoverFontColor; }

    &-link {
      display: block;
      padding: 3px 20px;

      color: inherit;
      text-decoration: none; } }

  &__menu_open {
    display: block; }

  &__menu::before {
    display: block;
    top: -20px;
    right: 2px;
    position: absolute;
    transform: rotateX(60deg);

    content: "▲";
    color: $mainTheme;
    font-size: 28px;
    z-index: -5; } }

@media screen and ( max-width: 770px ) {
  .user {

    &__menu {
      position: absolute; } } }

@media screen and ( max-width: 650px ) {
  .user {
    width: 100%;
    height: 65px;
    justify-content: space-around;

    &__menu {
      position: absolute;
      top: 100px;
      right: 10%; }

    &__button {
      width: 20px;
      height: 20px; } } }
